<!--  -->
<template>
  <div class=''>
      <NavBar>
          <div slot="left" @click="back" class="left">
             <img src="~assets/img/home/back.png" alt="">
          </div>
          <div slot="center" class="navtitile">
              <div v-for="(item , index) in titles" :key="index"
              class="titleitem" 
              :class="{titleActive:index==currentindex}" 
              @click="titleclick(index)"
              >{{item}}</div>
          </div>
          </NavBar>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import NavBar from 'components/common/navbar/NavBar.vue';
  export default {
    //import
  props:{
    //
  },
    components: {
        NavBar
    },
    data() {
      //
      return {
          titles:['','','',''],
          currentindex:0
      }
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
        titleclick(index){
            this.currentindex=index;
            this.$emit('titleclick',index);
        },
        back(){
            console.log('fanhui')
            this.$router.back()
        }
    },
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.navtitile{
    display: flex;
    font-size: 13px;
}
.titleitem{
    flex: 1;
}
.titleActive{
    color: pink;
}
.left img{
    height: 25px;
    width: 25px;
    margin-left: 10px;
    margin-top: 10px;
    opacity: 0.8;
}
</style>


