<template>
  <div id="shop_info" v-if="Object.keys(shop).length != 0">
    <div class="shop_name">
      <img :src="shop.logo" alt="" />
      <span>{{ shop.name }}</span>
    </div>

    <div class="score_info">
      <div class="score_left">
        <div class="score_sell">
          <div>{{ sellValue }}</div>
          <div style="font-size: 0.6rem;"></div>
        </div>
        <div class="score_item">
          <div>{{ shop.goods }}</div>
          <div style="font-size: 0.6rem;"></div>
        </div>
      </div>
      <div class="score_right">
        <table>
          <tr v-for="(item, index) in shop.score" :key="index">
            <td>{{ item.name }}</td>
            <td :style="{ color: item.isBetter ? 'red' : 'green' }">
              {{ item.score }}
            </td>
            <td
              style="color: white;"
              :style="{ backgroundColor: item.isBetter ? 'red' : 'green' }"
            >
              {{ item.isBetter ? "" : "" }}
            </td>
          </tr>
        </table>
      </div>
    </div>
    <div class="come_shop"></div>
  </div>
</template>

<script>
export default {
  props: {
    shop: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  computed: {
    sellValue() {
      /* parseFloat(this.shop.sells / 10000).toFixed(1) */
      return Math.round((this.shop.sells / 10000) * 10) / 10;
    },
    betterScore(isBetter) {
      if (isBetter) {
        return "{ backgroundColor: red, color: white}";
      } else {
        return "{ backgroundColor: green, color: white}";
      }
    }
  }
};
</script>

<style scoped>
#shop_info {
  padding: 0.64rem 0.34rem;
  border-bottom: 0.128rem solid rgba(128, 128, 128, 0.2);
  font-size: 0.75rem;
}
.shop_name{
  padding-left: 1.2rem;
}
.shop_name img {
  width: 1.7rem;
  height: 1.7rem;
  border: 0.042rem solid rgba(128, 128, 128, 0.5);
  border-radius: 0.85rem;
}
.shop_name span {
  margin-left: 0.34rem;
  position: relative;
  top: -0.64rem;
  font-size: 0.768rem;
}

/**
  * 
  * table
  */
.score_info {
  padding: 0px 0.512rem;
  display: flex;
}
.score_left,
.score_right {
  flex: 1;
}
/* score_left */
.score_left {
  display: flex;
  align-items: center;
}
.score_left > div {
  flex: 1;
}
.score_sell,
.score_item {
  text-align: center;
}
.score_item {
  border-right: 0.042rem solid gray;
}
/**
  * table
  */
.score_right > table {
  margin: 0 auto;
  font-size: 0.64rem;
  border-spacing: 0.42rem;
}
/**
  * 
  */
.come_shop {
  width: 6.4rem;
  height: 1.28rem;
  line-height: 1.28rem;
  text-align: center;
  border-radius: 0.42rem;
  background-color: rgba(128, 128, 128, 0.3);
  margin: 0 auto;
}
</style>



