<!--  -->
<template>
  <div class='tab-control'>
      <div v-for="(item, index) in titles" class="tab-control-item" :class="{tabcontrolactive:index===currentindex}"
      @click="itemclick(index)" :key="index">
          <span>{{item}}</span>
      </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

  export default {
    //import
  props:{
//
    titles:{
        type:Array,
        default(){
            return[]
        }
    }
  },
    components: {},
    data() {
      //
      return {
          currentindex:0
      }
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
        itemclick(index){
            this.currentindex=index;
            this.$emit('tabclick',index);//()
        }
    },
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.tab-control{
    display: flex;
    text-align: center;
    font-size: 15px;
}
.tab-control-item{
    flex: 1;
    height: 40px;
    line-height: 40px;
}
.tabcontrolactive{
    color: pink; 
    
}
.tabcontrolactive span{
    padding: 5px;
    border-bottom: 3px solid pink;
}
</style>


