<!--  -->
<template>
  <div class="tab-item" @click="itemClick">
      <div v-if="!isactive">
        <slot  name="item-icon"></slot>
      </div>
      <div v-else> <slot  name="item-icon-active"></slot></div>
      <div :class="{tabactive:isactive}"><slot  name="item-text"></slot></div>
      <!-- <div :style="activeColor"><slot  name="item-text"></slot></div> -->
<!-- <div :style="{color:'blue'}"><slot  name="item-text"></slot></div> -->

      <!-- <img src="../../assets/img/tabbar/b00.png" alt="">
     <div></div> -->
          
  </div>

</template>

<script>
//jsjsjson
//import  from '';

  export default {
    //import
    name:"tabbaritem",
    props:{
        path:String ,
        activeColor:{
          type:String,
          default:'red'
        }
    },
    components: {},
    data() {
      //
      return {
          // isactive:true
      }
    },
    // data
    computed: {
      isactive(){
        return this.$route.path.indexOf(this.path)!==-1//
      },
      acitiveStyle(){
        console.log(this.activeColor);
        return this.isactive ? {color : 'red'} : {color:'yellow'}
      }
      },
    //data
    watch: {},
    //
    methods: {
        itemClick(){
            console.log(this.path)
            // this.$router.push(this.path)
            this.$router.replace(this.path)
        }
    },
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style  scoped>

  .tab-item{
  flex: 1;
  text-align: center;
  height: 49px;
  font-size: 14px;
}
.tab-item img{
    width: 24px;
    height: 24px;
     
     margin-top: 3px;
     vertical-align: middle;
     margin-bottom: 2px;
}
.tabactive{
    color:blue;
}
</style>


