<script>
//jsjsjson
//import  from '';
import NavBar from 'components/common/navbar/NavBar.vue';
import cartinfo from './childComps/CartInfo.vue';
import carbottom from './childComps/CartBottomBar';
import Scroll from 'components/common/scroll/Scroll';
import CartBottomBar from './childComps/CartBottomBar.vue';

  export default {
    //import
    components: {
      NavBar,
      cartinfo,
      Scroll,
      carbottom,
        CartBottomBar
    },
    data() {
      //
      return {
        titlestr:''
      }
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {},
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.cart-nav{
  background-color:var(--color-tint);
  color: white;

  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 9;
}
.cart{
  height: 100vh;
}
.content{
    /* height: calc(100%-44px-49px); */
    position: absolute;
  /**/
  top: 44px;
  bottom: 89px;
  left: 0;
  right: 0;
  overflow: hidden;
}
.bottom{
  position: relative;
  height: 50px;
  width: 100%;
  bottom: 40px;
}
</style>


