        <span></span>
      </div>
      <div class="collect">
        <i class="icon"></i>
        <span></span>
      </div>
    </div>
    <div class="bottom_right">
      <div class="cart" @click="addToCart">
        
      </div>
      <div class="buy">
        
      </div>
    </div>
