<script>
//jsjsjson
//import  from '';

  export default {
    //import
  props:{
    //
  },
    components: {},
    data() {
      //
      return {}
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
        addToCart(){
         this.$emit('addToCart')
    }
    },
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
