    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {
      console.log('activated')
    }, //keep-alive
    }
</script>

<style scoped>

#tab-bar{
  display: flex;
  background-color: #f6f6f6;

  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;

  box-shadow: 0px -3px 1px rgba(100, 100, 100, 0.1);
}

</style>



