<!--  -->
<template>
  <div id='home'>
      <nav-bar class="home-nav"><div slot="center"></div></nav-bar>
      <TabControl class="tab-control" :titles="titles" @tabclick="tabclick" ref="tabcontrol1" v-show="istabFiex"></TabControl>
      <Scroll class="scroll-content" ref="scroll" 
      :probe_type="0" 
      @scroll="contentscroll" 
      :pullUpLoad="true"
      @pullingUp="loadmore">
        <!-- ref -->
        <Swiper>
      <SwiperItem v-for="(item, index) in banners" :key="index">
        <a :href="item.link">
          <img :src="item.image" @load="swiperimageload"/>
        </a>
      </SwiperItem>
    </Swiper>
  <HomeRecomendViews :recommends="recommends"></HomeRecomendViews>
 <TabControl class="tab-control1" :titles="titles" @tabclick="tabclick" ref="tabcontrol2"></TabControl>
 <GoodsList :goods="goods[type].list"></GoodsList>
 <!-- <GoodsList :goods="goods[type].list"></GoodsList>
  <GoodsList :goods="goods[type].list"></GoodsList> -->

      </Scroll>
      <back-top @click.native="backtopclick" v-show="isshowtop"></back-top>  
      <!--@click.native -->

  </div>
</template>

<script>
//jsjsjson
//import  from '';
import NavBar from 'components/common/navbar/NavBar.vue';
import Swiper  from "components/common/swiper/Swiper";
import SwiperItem  from "components/common/swiper/SwiperItem";
import TabControl from 'components/common/tabcontrol/TabControl';
import Scroll from 'components/common/scroll/Scroll';

import GoodsList from 'components/content/goods/GoodsList';
import BackTop from 'components/content/backtop/BackTop';

import Rcommends from "./childCompends/HomeRecomendViews";
import HomeRecomendViews from './childCompends/HomeRecomendViews.vue';


import {getHomeMultidata} from "network/home";
import {getHomeGoods} from "network/home";
import {debounce} from "common/untils";
  export default {
    //import
    components: {
        NavBar,
        Swiper,
        SwiperItem,
        Rcommends,
        HomeRecomendViews,
        TabControl,
        GoodsList,
        Scroll,
        BackTop
    },
    data() {
      //
      return {
        banners:[],
        recommends:[26],
        titles:['','',''],
        goods:{
          'pop':{page:0,list:[]},
          'new':{page:0,list:[]},
          'sell':{page:0,list:[]},
        },
        type:'pop',
        isshowtop:false,
        tabOffsetTop:0,
        istabFiex:false,
        saveScrollY:0,
        homeitemlistenner:null
        // goods:{
        //   'pop':{page:0,list:[]},
        //   'new':{page:0,list:[]},
        //   'sell':{page:0,list:[]},
        // }
      }
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {

//debounce
// debounce(func,delay){
//   let timer=null

//   return function(...args){
//     if(timer)clearTimeout(timer)
//     timer=setTimeout(() => {
//       func.apply(this,args)
//     }, delay);
//   }
// },


      //
  tabclick(index){
    if(index==0){
      this.type='pop'
    }
    if(index==1){
      this.type='new'
    }
    if(index==2){
      this.type='sell'
    }
    this.$refs.tabcontrol2.currentindex=index;
    this.$refs.tabcontrol1.currentindex=index;
    this.getHomeGoods(this.type);
  },
//
backtopclick(){
  console.log('dianji');
  this.$refs.scroll.scroll&&this.$refs.scroll.scroll.scrollTo(0,0,1000);//x/y,1000ms  scroll
},
contentscroll(position){
  // console.log(position)
  if(position.y<-300){
    this.isshowtop=true;
  }else{
    this.isshowtop=false;
  }
  //2.tabcontrol
  this.istabFiex=(-position.y)>this.tabOffsetTop
},
swiperimageload(){
  console.log(this.$refs.tabcontrol2.$el.offsetTop);
  //tabcontroloffsettop $el:
  this.tabOffsetTop=this.$refs.tabcontrol2.$el.offsetTop;
},
loadmore(){
  console.log('123')
  // const list=[{image:"https://gw.alicdn.com/bao/upload/O1CN01tSZ99L1D7Rby2PxYY_!!6000000000169-0-yinhe.jpg_Q75.jpg_.webp"},
  //         {image:"https://gw.alicdn.com/bao/upload/O1CN01RkMsv61kLzWITi1ft_!!6000000004668-0-yinhe.jpg_Q75.jpg_.webp"},
  //         {image:"https://gw.alicdn.com/bao/upload/O1CN01qCre101QT3J6XW1Sn_!!6000000001976-0-yinhe.jpg_Q75.jpg_.webp"},
  //         {image:"https://gw.alicdn.com/bao/upload/O1CN01P5mbFW1bJOjPUvp0q_!!6000000003444-0-yinhe.jpg_Q75.jpg_.webp"},
  //         {image:"https://gw.alicdn.com/bao/upload/O1CN01IvqSXT1ycqi5jDnsG_!!6000000006600-0-yinhe.jpg_Q75.jpg_.webp"},
  //         {image:"https://gw.alicdn.com/bao/upload/O1CN01LOID8T29J7QhS5V8j_!!6000000008046-0-yinhe.jpg_Q75.jpg_.webp"},
  //         {image:"https://gw.alicdn.com/bao/upload/O1CN01LOID8T29J7QhS5V8j_!!6000000008046-0-yinhe.jpg_Q75.jpg_.webp"}];
  // this.goods[this.type].list=[...this.goods[this.type].list, ...list];
  this.getHomeGoods(this.type);
  this.$refs.scroll.scroll&&this.$refs.scroll.scroll.finishPullUp();//
  //this.$refs.scroll.scroll.refresh();//scroll
},
      //
      getHomeMultidata(){
          getHomeMultidata().then(res=>{
           console.log(res);
          this.banners=res.data.banner.list;
          this.recommends=res.data.recommend.list;
        })
      },
      getHomeGoods(type){
        //
        const page=this.goods[type].page+1;
        getHomeGoods(type,page).then(res=>{
          console.log(res);
          this.goods[type].list.push(...res.data.list);//for
          this.goods[type].page+=1;
          
        })
      }
    },
    // - this
    created() {
      this.getHomeMultidata();
      this.getHomeGoods('pop');
      // this.getHomeGoods('new');
      // this.getHomeGoods('sell');
      // getHomeMultidata().then(res=>{
      //   // console.log(res);
      //   this.banners=res.data.banner.list;
      //   this.recommends=res.data.recommend.list;
      // })
      //
      // getHomeGoods('pop',1).then(res=>{
      //   console.log(res);
      // })

    },
    // - DOM
    mounted() {
     const brefresh=debounce(this.$refs.scroll.refresh,20);
     console.log(brefresh)
      //3.item
      this.homeitemlistenner=()=>{
        console.log('---');  
        brefresh()
        }
    this.$bus.$on('itemimageload',this.homeitemlistenner)
      // this.$bus.$on('itemimageload',()=>{
      //   console.log('---');  
      //   brefresh()
      //   //  this.$refs.scroll.scroll&&this.$refs.scroll.refresh();//scroll
      //   })
      //2tabcontroloffsettop $el:

    },
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // -   "better-scroll": "^1.13.2",
    destroyed() {

    }, // - 
    activated() {
      console.log('activated')
      this.$refs.scroll.scroll.scrollTo(0,this.saveScrollY,0)
      this.$refs.scroll.refresh()
    }, //keep-alive
    deactivated() {
      //Y
      this.saveScrollY=this.$refs.scroll.getscrollY()
    //
    this.$bus.$off('itemimageload',this.homeitemlistenner)
      console.log('deactivated'+this.saveScrollY)
;
    }, //keep-alive
  }
</script>
<style scoped>
#home{
  padding-top: 44px;
  height: 100vh;
  /*  100vw*/
  /* position: relative; */
}
.home-nav{
  background-color:var(--color-tint);
  color: white;

  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 9;
}
/* .tab-control{
  position: sticky; */
  /**/
  /* top: 44px;
  background-color: white;
  z-index: 9;
} */
.tab-control{
  background-color: white;
  position: relative;
  /* position: fixed;
  width: 100%; */
  z-index: 9;
}
.scroll-content{
  /* height: 300px;
  background-color: red; */
  overflow:hidden;/**/
  position: absolute;
  /**/
  top: 44px;
  bottom: 49px;
  left: 0;
  right: 0;
}
/*
.content{
  height: calc(100%-93px);/*calc css3css*/
  /*overflow: hidden;*/
  /* margin-top: 44px; */
/*}*/

</style>


