<!--ref   -->
<template>
  <div class='wrapper' ref="wrapper">  
      <div class="content">
          <slot></slot>
      </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import BetterScroll from 'better-scroll'
  export default {
    //import
  props:{
    //
    probe_type:{
      type:Number,
      default:0
    },
    pullUpLoad:{
      type:Boolean,
      default:false
    }
  },
    components: {},
    data() {
      //
      return {
          scroll:null
      }
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
      refresh(){
        return this.scroll.refresh();
      },
      getscrollY(){
        return this.scroll?this.scroll.y:0;
      }
    },
    // - this
    created() {},
    // - DOM
    mounted() {
        this.scroll=new BetterScroll(this.$refs.wrapper,{
        probeType:this.probe_type,//01 2  3
        pullUpLoad:this.pullUpLoad,
        click:true
      })//
      this.scroll.on('scroll',(position)=>{
        // console.log(position);
        this.$emit('scroll',position);
      })
      this.scroll.on('pullingUp',()=>{
        console.log('');
        this.$emit('pullingUp')
        //
        //  setTimeout(() => {
        //   this.scroll.finishPullUp()
        //   }, 2000);
        
      })
    },
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
</style>


