<!--  -->
<template>
<div class="amap-page-container">
    <div :style="{width:'100%',height:'300px'}">
      <el-amap vid="amap" :plugin="plugin" class="amap-demo" :center="center" :zooms="[3,5]">
        <el-amap-marker v-for="(marker,index) in markers" 
        :position="marker.position" 
        :key="index" 
        :content="marker.icon" 
        :icon="marker.icon" class="marker">

        </el-amap-marker>
      </el-amap>
    </div>


    <div class="toolbar">
        <span v-if="loaded">
          location: lng = {{ lng }} lat = {{ lat }}
        </span>
      <span v-else></span>
    </div>
    <div
      v-on:click="location()"
    >
      
    </div>
     <div
      v-on:click="newbeasier()"
    >
    </div>
    <div id="myChart" style="width: 100%;height:400px;">123</div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import BetterScroll from 'better-scroll'
import blue from "@/assets/img/tabbar/b00.png"

let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/line");
// 
require("echarts/lib/component/tooltip"); // tooltip
require("echarts/lib/component/title"); //  title
require("echarts/lib/component/legend"); // legend

  export default {
    //import
    components: {},
    data() {
      //
      let self = this
      return {
        
        scroll:null,
        data: [],
        markers: [],
         center: [121.59996, 31.197646],
            lng: 0,
            lat: 0,
            loaded: false,
            plugin: [{
              enableHighAccuracy: true,//:true
              timeout: 100,          //10
              maximumAge: 0,           //00
              convert: true,           //true
              showButton: true,        //true
              buttonPosition: 'RB',    //'LB'
              showMarker: true,        //true
              showCircle: true,        //true
              panToLocation: true,     //true
              zoomToAccuracy:true,//f
              extensions:'all',
              pName: 'Geolocation',
              doubleClickZoom:true,
              events: {
                init(o) {
                  // o 
                  o.getCurrentPosition((status, result) => {
                    console.log(result)
                    if (result && result.position) {
                      self.lng = result.position.lng;
                      self.lat = result.position.lat;
                      self.center = [self.lng, self.lat];
                      self.loaded = true;
                      self.$nextTick();
                    }
                  });
                }
              }
            }]
          
      }
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
      location(){
         var citySearch = new AMap.PlaceSearch();
                     citySearch.getLocalCity(function (status, result) {
                         if (status === 'complete' && result.info === 'OK') {
                             // result
                             let province = result.province;
                             let city = result.city;
                             console.log('123'+province)
                         }
                     })
                
      },
      newbeasier(){
        this.$router.push('/newbeasier');
      },
      myEcharts(){
		  // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
          tooltip: {
            trigger: 'axis',
            axisPointer: { // 
              type: 'shadow' // 'line' | 'shadow'
            }
          },
          legend: {
            data: ['', '', '', '', '', '', '', '', '']
          },
          toolbox: {
            show: true,
            orient: 'vertical',
            x: 'right',
            y: 'center',
            feature: {
              mark: {
                show: true
              },
              dataView: {
                show: true,
                readOnly: true
              },
              magicType: {
                show: true,
                type: ['line', 'bar', 'stack', 'tiled']
              },
              restore: {
                show: true
              },
              saveAsImage: {
                show: true
              }
            }
          },
          calculable: true,
          xAxis: [{
            type: 'category',
            data: ['', '', '', '', '', '', '']
          }],
          yAxis: [{
            type: 'value',
            scale: true,
              name: '',
              max: 180,
              min: 0

          }],
          series: this.data, // 
          dataZoom: [{ //   Y X
              type: 'inside',
              start: 0,
              end: 80
            },

            {
              start: 0,
              end: 80, //         base64  url  inco 
              handleIcon: '',
              handleSize: '80%', //  
              handleStyle: { // 
                color: '#fff',
                shadowBlur: 3,
                shadowOffsetX: 4,
                shadowOffsetY: 2,
                transform: "rolate(90deg)" // 
              }
            }

          ],
        });
      }
    },
    // - this
    created() {
      let self = this;
        let markers = [];
        let index = 0;
 
        let basePosition = [119.59996, 26.037646];
        let num = 10;
 
        // content**********
        for (let i = 0 ; i < num ; i++) {
          markers.push({
            position: [basePosition[0], basePosition[1] + i * 1],
            content: `content ${i}`,
            icon:require('./b00.png')
          });
        }
        this.markers = markers;
      
     //  created     this.drawLine();       echarts   created hook: "TypeError: Cannot read property 'getAttribute' of null"
     
      /*   axios.post('*********', ).then(res => { */
          var arr = []  // Ehcarts  
          for (var i = 0; i < 10; i++) {
            var json = {
              name: '' + i,
              type: 'line',
              stack: '',
              data: [150 + i, 2 - i, 20 * i, 154 - 100, 19 - 8, 30, 40]
            }
            arr.push(json)
          }
          this.data = arr
          //  echartsv-if 
         // this.drawLine();      axios     created hook: "TypeError: Cannot read property 'getAttribute' of null" 
      /*    }) */
    
    },
    // - DOM
    mounted() {
      this.myEcharts();
      // this.scroll=new BetterScroll('.wrapper1',{
      //   movable: true,
      //   zoom: true,
      //   probeType:3,//01 2  3
      //   pullUpLoad:true,
      //   click:true
      // })//
      // this.scroll.on('scroll',(position)=>{
      //   // console.log(position);
      // })
      // this.scroll.on('pullingUp',()=>{
      //   console.log('');
      //   //
      //    setTimeout(() => {
      //     this.scroll.finishPullUp()
      //     }, 2000);
        
      // })
    },
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.wrapper1{
  height: 200px;
  background-color: red;

  /* overflow-y: scroll; */
}
.amap-demo {
    height: 300px;
  }
  .amap-icon img{
    width: 10px;
    height: 10px;
  }
</style>


