<!--  -->
<template>
  <div class='goods'>
<!-- ul>li{$}*10 -->
<goods-list-item v-for="(item,index) in goods" :goodsitem="item" :key="index"></goods-list-item>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import GoodsListItem from './GoodsListItem.vue'
  export default {
    //import
  props:{
    //
    goods:{
        type:Array,
        default(){
            return[]
        }
    }
  },
    components: {
        GoodsListItem
    },
    data() {
      //
      return {}
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {},
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.goods{
    display: flex;/**/
    flex-wrap: wrap;
    justify-content: space-around;/**/
    padding: 2px;
}

</style>


