<!--  -->
<template>
  <div class='goodlist-item' @click="itemclick">
      <!-- <img :src="showImage" alt="" @load="imageload"> -->
      <img v-lazy="showImage" alt="" @load="imageload">
      <div class="goods-info">
          <p>{{goodsitem.title}}</p>
          <span class="price">{{goodsitem.price}}</span>
          <span class="collect">{{goodsitem.sale}}</span>
        </div>
      </div>
</template>

<script>
//jsjsjson
//import  from '';

  export default {
    //import
  props:{
    //
    goodsitem:{
        type:Object,
        default(){
            return{}
        }
    }
  },
    components: {

    },
    data() {
      //
      return {}
    },
    // data
    computed: {
      showImage(){
        return this.goodsitem.image||this.goodsitem.show.img
      }
    },
    //data
    watch: {},
    //
    methods: {
      imageload(){
        // console.log('');
        this.$bus.$emit('itemimageload');
      },
      itemclick(){
        console.log('xiangqing');
        this.$router.push('/detail/'+this.goodsitem.iid);
        // this.$router.push({
        //   path:'/deatil',
        //   query:{

        //   }
        // })
      }
    },
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.goodlist-item{
    padding-bottom: 40px;
    position: relative;
    width: 48%;
}
.goodlist-item img{
   width: 100%;
   border-radius: 5px;
}
.goods-info{
    font-size: 12px;
    position: absolute;
    bottom: 5px;
    left: 0;
    right: 0;
    overflow: hidden;
    text-align: center;
}
.goods-info p{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin-bottom: 3px;
}
.goods-info .price{
    color: pink;
    margin-right: 20px;
}
.goods-info .collect{
    position: relative;
}
.goods-info .collect::before{
    position: absolute;
    content: '';
    left: -15px;
    top: -1px;
    width: 14px;
    height: 14px;
    background: url("~assets/img/tabbar/b00.png") 0 0/14px 14px;
}
</style>


