<!--  -->
<template>
  <div id="detail_params">
      <table  class="params_rule" >
          <tr v-for="(item,index) in paramInfo.rule.tables[0]" :key="index">
              <td v-for="(tritem,trindex) in item" :key="trindex">
            {{ tritem }}
        </td>
          </tr>
        
    </table>

    <table  class="params_set">
        <tr v-for="(tritem,trindex) in paramInfo.info.set" :key="trindex">
          <th >
            {{ tritem.key}}
          </th>
          <td >
            {{ tritem.value}}
          </td>
        </tr>
    </table>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

  export default {
    //import
  props:{
    //
    paramInfo: {
      type: Object,
      default() {
        return {};
      }
    }
  },
    components: {},
    data() {
      //
      return {}
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {},
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
#detail_params {
  font-size: 0.65rem;
  padding: 0.42rem 0rem;
  border-bottom: 0.3rem solid rgba(128, 128, 128, 0.2);
}
.params_rule,
.params_set {
  border-bottom: 0.1rem solid rgba(128, 128, 128, 0.5);
  border-collapse: collapse; /* tr */
  margin: 0 auto;
  margin-bottom: 0.42rem;
  width: 90%;
}
.params_rule tr,
.params_set tr {
  border-bottom: 0.04rem solid rgba(128, 128, 128, 0.5);
}

.params_rule th {
  text-align: left;
  padding-right: 1rem;
  white-space: nowrap;
}
.params_rule td {
  padding: 0.5rem;
}

.params_set th {
  text-align: left;
  padding: 0.5rem 3.5rem 0.5rem 0rem;
  white-space: nowrap;
}
.params_set td {
  color: var(--color-high-text);
}

.info_img img {
  width: 100%;
}

</style>


