<!--  -->
<template>
  <div class=''>
       <div id="myChart" style="width: 100%;height:400px;">123</div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
let echarts = require("echarts/lib/echarts");
require("echarts/lib/chart/line");
// 
require("echarts/lib/component/tooltip"); // tooltip
require("echarts/lib/component/title"); //  title
require("echarts/lib/component/legend"); // legend
  export default {
    //import
  props:{
    //
  },
    components: {},
    data() {
      //
      return {}
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {
        initChart(){
            // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
            backgroundColor:'#2c3e50',
            title:{//
                top:20,
                text:'',
                subtext:'',
                textStyle:{
                    fontSize:16,
                    color:'#F1F1F3'
                },
                left:'1%'
            },
            //
            tooltip: {
            show:false,
            trigger: 'axis',
            axisPointer: { // 
              lineStyle:{
                  color:'#57617B'
              }
            }
          },
        //   legend: {
        //     top:20,
        //     icon:'circle',
        //     itemWidth:14,
        //     itemHeight:5,
        //     itemGap:13,
        //     data: ['', '', ''],
        //     right:'4%',
        //     textStyle:{
        //             fontSize:12,
        //             color:'#F1F1F3'
        //         }
        //   },
          grid:{
              top:100,
              left:'2%',
              right:'2%',
              bottom:'2%',
              containLabel:true
          },
          
          xAxis: [{
            type: 'category',
            boundaryGap:true,//boundaryGapfalsetrue
            axisLine:{
                lineStyle:{
                    color:'#57617B',
                    
                }
            },
            axisTick: {
                show: false//
                },
            data: ['13:00', '13:05','13:10','13:10','13:20','13:00','13:30','13:00','13:00','13:00','13:00','13:00'],
            // splitLine:{//grid
            //     show:true,
            //     lineStyle:{
            //         color:'#57617B',
            //         width: 1
            //     }
            // }
            
          }],
          yAxis: [{
            //  offset:20, // 
            start: 10,
            type: 'value',
            name:'(%)',
            min: 0, // 
            max: 105, // ,3
            splitNumber: 7, // 
            interval: 15, // 
            
            axisTick:{
                show:false//Y
            },
            axisLine:{
                show:true,
                lineStyle:{
                    color:'#57617B'
                }
            },
            axisLabel:{
                margin:10,
                textStyle:{
                    fontSize:14
                }
            },
            splitLine:{//grid
                show:true,
                lineStyle:{
                    color:'#57617B',
                    type:'dashed',
                    width: 1
                }
            },
         
        

          }],
           dataZoom: [
          {
            // start: 0, //0
            // end: 100 - 1500 / 31, //100
            endValue: 6, // 7
            type: "slider",
            
            show: false,
            xAxisIndex: [0],
            handleSize: 0, // 2
            height: 10, //
            left: "10%", //
            right: "10%", //
            bottom: 26, //
            borderColor: "#000",
            fillerColor: "#269cdb",
            borderRadius: 5,
            backgroundColor: "#33384b", //
            showDataShadow: false, // auto
            showDetail: false, // true
            realtime: true, //
            filterMode: "filter",
          },
          //
          {
            type: "inside",
            show: false,
            xAxisIndex: [0],
            start: 0, //1
            end: 100 - 1500 / 31, //100
          },
        ],
          series: [{
              name:'1123',
              type:'line',
              smooth:true,
              symbol:'circle',
            //   showSymbol: false, // ,
            symbolSize: 7, // 
              data: [76, 60, 70, 80, 90, 100,77, 60, 55, 80, 90, 100],
              lineStyle:{
                  normal:{
                      width:2
                  },
             
              },
              areaStyle:{
                  normal:{
                      color:new echarts.graphic.LinearGradient(0,0,0,1,[{
                          offset:0,
                          color:'rgba(137,189,27,0.3)'
                      },
                      {
                          offset:0.8,
                          color:'rgba(137,189,27,0)'
                      }],false),
                      shadowColor:'rgba(0,0,0,0.1)',
                      shadowBlur:10
                  }
              }
          },
          {
              name:'1123',
              type:'bar',
            //   symbol:'circle',
              barWidth : 10,
            //   showSymbol: false, // ,
            symbolSize: 7, // 
              data: [76, 60, 70, 80, 90, 100,77, 60, 55, 80, 90, 100],
              lineStyle:{
                  normal:{
                      width:1
                  },
             
              },
              itemStyle: {         //
            normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{    //
                    offset: 0,
                    color: '#fccb05'
                }, {
                    offset: 1,
                    color: '#f5804d'
                }]),
                // barBorderRadius: 12,
            }},
          }]
        })
        }
    },
    // - this
    created() {},
    // - DOM
    mounted() {
        this.initChart();
    },
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
</style>


