<!--  -->
<template>
  <div class='detail-comment'>
      <!-- {{info}} -->
      <div class="title" >
          <div class="title-left">
          
      </div>
      <div class="more" >
          
      </div></div>
      
      <div class="user">
          <img :src="info.user.avatar" alt="" >
          <span >{{info.user.uname}}</span>

      </div>
      <div class="content">
          {{info.content}}
          <p class="content_info">
          {{ info.created | dateShow}} {{ info.style }}
        </p>
      </div>
      <div class="image" v-for="(item,index) in info.images" :key="index">
          <img :src="item" alt="">

      </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';
import {formatDate} from "common/untils";
  export default {
    //import
  props:{
    //
    info:{
        type:Object,
        default(){
            return {}
        }
    }
    
  },
  filters: {
    dateShow(value) {
      let date = new Date(value * 1000);
      return formatDate(date, "yyyy-MM-dd");
    }
  },
    components: {},
    data() {
      //
      return {}
    },
    // data
    computed: {},
    //data
    watch: {},
    //
    methods: {},
    // - this
    created() {},
    // - DOM
    mounted() {},
    beforeCreate() {}, // - 
    beforeMount() {}, // - 
    beforeUpdate() {}, // - 
    updated() {}, // - 
    beforeDestroy() {}, // - 
    destroyed() {}, // - 
    activated() {}, //keep-alive
  }
</script>
<style scoped>
.detail-comment{
 position: relative;
    font-size: 11px;
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
}
.title-left{

  position: absolute;
  left: 10px;
  font-size: 15px;
}
.more{
 position: absolute;
    right: 10px;
font-size: 15px;
}
.user {
  padding: 30px 0;
  line-height: 1.8rem;
  display: flex;
}
.user img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 0.04rem solid rgba(128, 128, 128, 0.3);
}
.user span {
  margin-left: 15px;
  height: 50px;
  margin-top: 5px;
}
.content{
    margin-top: -30px;
    line-height: 1rem;
  color: #666;
}
.content_info {
  color: #999;
  padding: 0.3rem 0;
}
.image{

    display:inline-block; 
    /* float: left; */
}
.image img{
    width:110px;
    height: 100px;
    margin-left: 3px;
}
</style>


